<template>
  <div>
    <el-table :data="sotlistData" style="width: 90%;margin-top: 20px;">
      <el-table-column label="名称" prop="soft_name"></el-table-column>
      <el-table-column label="版本" prop="soft_ver"></el-table-column>
      <el-table-column label="描述" prop="soft_desc"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope>
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <el-button
            v-if="!scope.row.is_install"
            size="mini"
            icon="el-icon-circle-plus"
            @click="handleInstall(scope.row.nid,scope.row.soft_name,scope.row.soft_ver)"
          >安装</el-button>
          <el-button
            v-else
            type="danger"
            size="mini"
            icon="el-icon-remove"
            @click="handleUninstall(scope.row.nid)"
          >卸载</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="安装方式" :visible.sync="dialogFormVisible">
      <el-form :model="installform">
        <el-form-item label="软件名称" :label-width="formLabelWidth">
          <el-input v-model="installform.soft_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="安装方式" :label-width="formLabelWidth">
          <el-radio v-model="installform.installtype" label="rpm">极速</el-radio>
          <el-radio v-model="installform.installtype" label="bash">编译</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="InstallSoft">安 装</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Soft",
  data() {
    return {
      nid: "",
      sotlistData: [],
      search: "",
      dialogFormVisible: false,
      installform: {
        nid: "",
        soft_name: "",
        installtype: ""
      },
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    onGetsoft() {
      this.$http
        .getsoft(this.nid)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            this.sotlistData = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleInstall(nid, soft_name, soft_ver) {
      this.dialogFormVisible = true;
      this.installform.nid = nid;
      this.installform.soft_name = soft_name + soft_ver;
      this.installform.installtype = "rpm";
    },
    handleUninstall() {},
    InstallSoft() {
      this.dialogFormVisible = false;
      console.log(this.installform);
      this.$http
        .upsoft(this.installform)
        .then(res => {
          if (res.errcode === 0) {
            let data = res.data;
            this.$message({
              message: res.errmsg,
              type: "success"
            });
            this.onGetsoft();
          }
        })
        .catch(error => {
          if (error.response) {
            this.errmsg = error.response.data.errmsg;
            this.$message.error(this.errmsg);
          } else {
            console.log(error.message);
          }
          console.log(error.config);
        });
    }
  },
  created() {
    this.onGetsoft();
  }
};
</script>

<style>
</style>


